body {
  margin: 0;
  padding: 0;
}

.search-form {
  background-color: #0000b5;
  justify-content: center;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

.gallery {
  max-width: 1140px;
  grid-gap: 12px;
  grid-template-columns: repeat(auto-fit, minmax(320px, 1fr));
  grid-auto-rows: 240px;
  justify-content: center;
  margin-left: auto;
  margin-right: auto;
  padding: 0;
  list-style: none;
  display: grid;
}

.gallery__image {
  height: 100%;
  width: 100%;
  object-fit: cover;
}

.gallery__link {
  height: 100%;
  text-decoration: none;
  display: block;
}

